import React from "react";
import "./Home.css";
import Stars from "../../StarSky/Stars";
const Home = (props) => {
  return (
    <div className="Home">
      <Stars />
      <div className="Intro--container">
        <div className="Circle"></div>
        <div className="Info--container">
          <h1 datatext="Welcome to my portfolio" className="Glitch">
            Welcome to my portfolio
          </h1>
          <p className="Glitch">I am a frontend/React.JS developer</p>
        </div>
        <div className="Mouse--indicator">
          <div className="Mouse--icon">
            <div className="Indicator"></div>
          </div>
        </div>
      </div>
      <div className="Social--media">
        <button>
          <a href="https://github.com/MadalinNov" target="_blank">
            <i className="fab fa-github-alt"></i>
          </a>
        </button>
        <button>
          <a
            href="https://www.linkedin.com/in/novac-madalin-7b6280166/"
            target="_blank"
          >
            <i className="fab fa-linkedin-in"></i>
          </a>
        </button>
        <button>
          <a href="../../../Images/CVMadalinNovac.pdf" download target="_blank">
            CV
          </a>
        </button>
      </div>
    </div>
  );
};
export default Home;
