/* eslint-disable react/jsx-no-target-blank */
import React from "react";
import "./Card.css";
import FadeInSection from "../FadeInSection/FadeInSection";
const Card = (props) => {
  const cardImage = [
    {
      image: process.env.PUBLIC_URL + "/Images/Portfolio-v1.PNG",
      description: "Portfolio V1.0",
      technologies: "HTML, CSS, JavaScript, jQuery",
      briefDescription:
        "This is my first portfolio project wich, the project itself, will provide my skills level.",
      demo: "https://madalinnov.github.io/portofolioWebsite/",
      code: "https://github.com/MadalinNov/portofolioWebsite",
      id: 1,
    },
    {
      image: process.env.PUBLIC_URL + "/Images/checkout.PNG",
      description: "PC Peripherals store",
      technologies: "React, CommerceJs, StripeJs, JavaScript, CSS, React Hooks",
      briefDescription:
        "Fully working eCommerce website with backend integrated from CommerceJS and payment form used with StripeJS.",
      demo: "https://madalinnov.github.io/pc-peripherals-store/#/",
      code: "https://github.com/MadalinNov/pc-peripherals-store/tree/gh-pages",
      id: 4,
    },
    {
      image: process.env.PUBLIC_URL + "/Images/Recipes-app.PNG",
      description: "React Recipes App",
      technologies: "React.js, Javascript, Sass, API",
      briefDescription:
        "This React.JS has API calling implemented. It is able to fetch, search, filter and display search results.",
      demo: "https://madalinnov.github.io/recipes-app/",
      code: "https://github.com/MadalinNov/recipes-app",
      id: 2,
    },

    {
      image: process.env.PUBLIC_URL + "/Images/Basket-app.PNG",
      description: "React Basket App",
      technologies: "React.js, Javascript, Sass",
      briefDescription:
        "React.JS basket app. It stores selected items into a virtual cart, it also has a search filter implemented.",
      demo: "https://madalinnov.github.io/react-basket-app/",
      code: "https://github.com/MadalinNov/react-basket-app",
      id: 7,
    },
    {
      image: process.env.PUBLIC_URL + "/Images/Form-validation.PNG",
      description: "Form Validation",
      technologies: "HTML, Javascript, CSS, JSON",
      briefDescription:
        "This form validation is able to validate user input corectly and it also stores user data to local storage.",
      demo: "https://madalinnov.github.io/Register-Login-Sistem/",
      code: "https://github.com/MadalinNov/Register-Login-Sistem",
      id: 3,
    },
    {
      image: process.env.PUBLIC_URL + "/Images/Barber-shop.PNG",
      description: "Barber Shop",
      technologies: "HTML, CSS, Bootstrap",
      briefDescription:
        "This website comes with prices list, displaiment of different hairstyles and a contact form for appointments.",
      demo: "https://madalinnov.github.io/barber-shop-website-recreation/",
      code: "https://github.com/MadalinNov/barber-shop-website-recreation",
      id: 5,
    },
    {
      image: process.env.PUBLIC_URL + "/Images/shoes.PNG",
      description: "Shopping Cart",
      technologies: "HTML, Sass, Javascript",
      briefDescription: "Landing Page",
      demo: "https://madalinnov.github.io/shoes-store/",
      code: "https://github.com/MadalinNov/shoes-store",
      id: 6,
    },
  ];

  return cardImage.map((image) => {
    return (
      <FadeInSection key={image.id}>
        <div className="Card">
          <div className="Card--body">
            <img src={image.image} alt="" />
            <span>{image.description}</span>
          </div>
          <div className="Popup">
            <div className="Info">
              <div className="Spacing--div">
                <p className="Description">{image.briefDescription}</p>
                <p className="Tech--used">
                  Used technologies: {image.technologies}
                </p>
                <div className="Btns">
                  <button>
                    <a href={image.demo} target="_blank">
                      Demo
                    </a>
                  </button>
                  <button>
                    <a href={image.code} target="_blank">
                      Code
                    </a>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </FadeInSection>
    );
  });
};
export default Card;
